import React from 'react';

type TypeIntersectionFix<Base, Hoc> = Base & Omit<Hoc, keyof Base>;
type TypedComponent<Base, Hoc> = React.FC<TypeIntersectionFix<Base, Hoc>>;

export function getStoryComponent<Base, Hoc>(defaults: Base, hocDefaults: Hoc): TypedComponent<Base, Hoc> {
  type ExtendedProps = TypeIntersectionFix<Base, Hoc>;
  const StoryComponent: TypedComponent<Base, Hoc> = () => null;

  // пропсы дефолты компонента затирают дефолты хока - важно следить, чтобы это соответствовало реализации компонента
  StoryComponent.defaultProps = {
    ...hocDefaults,
    ...defaults
  } as ExtendedProps;

  ['displayName', '__docgenInfo'].forEach(key => {
    /* @ts-ignore *//* eslint-disable-line @typescript-eslint/ban-ts-comment */
    delete StoryComponent.defaultProps[key];
  });

  return StoryComponent;
}
